import {
  Table,
  Group,
  Text,
  ActionIcon,
  rem,
  CopyButton,
  Tooltip,
} from "@mantine/core";
import { IconCopy, IconSearch, IconCheck } from "@tabler/icons-react";
import DOMPurify from "dompurify";

interface SearchResult {
  AUTHOR: string;
  QUOTE?: string;
  document?: string;
}

interface SearchResultDisplayProps {
  data: SearchResult[];
  relatedSearch: any;
}

export function SearchResultDisplay({
  data,
  relatedSearch,
}: SearchResultDisplayProps) {
  const rows = data.map((item) => (
    <Table.Tr key={item.QUOTE || item.document}>
      <Table.Td>
        <Group gap="sm">
          <div>
            <Text
              fz="sm"
              fw={500}
              ta="left"
              dangerouslySetInnerHTML={{
                __html: DOMPurify.sanitize(
                  item.QUOTE?.toString() || item.document?.toString() || ""
                ),
              }}
            />

            <Text c="dimmed" fz="xs" ta="left" fs="italic">
              ~{item.AUTHOR}
            </Text>
          </div>
        </Group>
      </Table.Td>

      <Table.Td>
        <Group gap={0} justify="flex-end">
          <CopyButton
            value={`${item.QUOTE || item.document}\n~${item.AUTHOR}`}
            timeout={2000}
          >
            {({ copied, copy }) => (
              <Tooltip
                label={copied ? "Copied" : "Copy"}
                withArrow
                position="top"
              >
                <ActionIcon
                  color={copied ? "teal" : "gray"}
                  variant="subtle"
                  onClick={copy}
                >
                  {copied ? (
                    <IconCheck style={{ width: rem(16) }} />
                  ) : (
                    <IconCopy style={{ width: rem(16) }} />
                  )}
                </ActionIcon>
              </Tooltip>
            )}
          </CopyButton>
        </Group>
      </Table.Td>

      <Table.Td>
        <Group gap={0} justify="flex-end">
          <Tooltip label="Search Similar">
            <ActionIcon variant="subtle" color="gray">
              <IconSearch
                style={{ width: rem(16), height: rem(16) }}
                stroke={1.5}
                onClick={() => relatedSearch(item.QUOTE || item.document)}
              />
            </ActionIcon>
          </Tooltip>
        </Group>
      </Table.Td>
    </Table.Tr>
  ));

  return (
    <Table.ScrollContainer minWidth={500}>
      <Table verticalSpacing="md" highlightOnHover>
        <Table.Tbody>{rows}</Table.Tbody>
      </Table>
    </Table.ScrollContainer>
  );
}
