import React, { useState } from "react";
import {
  TextInput,
  ActionIcon,
  useMantineTheme,
  rem,
  Radio,
  SegmentedControl,
} from "@mantine/core";
import {
  IconSearch,
  IconArrowRight,
  IconAdjustments,
} from "@tabler/icons-react";
import { SearchResultDisplay } from "./SearchResultDisplay";
import { getHotkeyHandler } from "@mantine/hooks";
import { Container } from "@mantine/core";
import { Flex, Button, Box, Text, Group, Space } from "@mantine/core";
import { CloseButton } from "@mantine/core";
import { Textarea } from "@mantine/core";
import { useWindowScroll } from "@mantine/hooks";
import { useEffect } from "react";

interface Quote {
  AUTHOR: string;
  QUOTE: string;
}

interface ApiResponse {
  result: Quote[];
}

export function Searchbar() {
  const [value, setValue] = useState("");
  const [isNeural, setIsNeural] = useState("true");
  const [searchResult, setSearchResult] = useState<Quote[] | null>(null);

  const theme = useMantineTheme();

  const [scroll, scrollTo] = useWindowScroll();

  function convertStringToBoolean(value: string): boolean | null {
    if (value === "true") {
      return true;
    } else if (value === "false") {
      return false;
    } else {
      return null; // or throw an error if you want to handle unexpected values differently
    }
  }

  const handleSearch = (optionalValue?: string) => {
    const input = optionalValue ?? value;

    console.log(
      "Submitting value:",
      input,
      "neural=",
      convertStringToBoolean(isNeural)
    );

    // Make an API call here
    fetch(
      `https://quote-quest-qdt4jyosjq-uc.a.run.app/api/search?q=${input}&neural=${convertStringToBoolean(
        isNeural
      )}`
    )
      .then((response) => response.json())
      .then((data: ApiResponse) => {
        // Process the API response data
        console.log("API Response:", data);

        // Update the state with the response data
        setSearchResult(data.result);
      })
      .catch((error) => {
        console.error("Error while making API call:", error);
        // Handle the error, show a message, etc.
      });
  };

  const handleSubmit = () => {
    handleSearch();
  };

  const clearValue = () => {
    setValue("");
    setSearchResult(null);
  };

  const segmentfunction = (value: string) => {
    setSearchResult(null);
    setIsNeural(value);
  };

  useEffect(() => {
    // This effect will run after the component has rendered and state is updated
    handleSubmit();
  }, [isNeural]);

  const relatedSearch = (quoteText: any) => {
    console.log(quoteText);
    setValue(quoteText);
    handleSearch(quoteText);
    scrollTo({ y: 0 });
  };
  return (
    <Container size="xs">
      <div>
        <SegmentedControl
          value={isNeural}
          color="rgba(49, 151, 214, 1)"
          onChange={segmentfunction}
          radius={30}
          size="md"
          data={[
            { label: "Neural", value: "true" },
            { label: "Text", value: "false" },
          ]}
          transitionDuration={300}
        />
        <Space h="sm" />
        <div>
          <Textarea
            radius="md"
            size="md"
            placeholder="Search for quotes"
            rightSectionWidth={42}
            autosize
            leftSection={
              <IconSearch
                style={{ width: rem(18), height: rem(18) }}
                stroke={1.5}
              />
            }
            rightSection={
              <div>
                {value !== "" && (
                  <ActionIcon onClick={clearValue} variant="transparent">
                    <CloseButton variant="transparent"></CloseButton>
                  </ActionIcon>
                )}
              </div>
            }
            value={value}
            onChange={(event) => setValue(event.currentTarget.value)}
            onKeyDown={getHotkeyHandler([["Enter", handleSubmit]])}
          />
        </div>

        <Space h="md" />

        <Box>
          <Group justify="center">
            <Text>Try this:</Text>
            <Button
              variant="gradient"
              gradient={{ from: "blue", to: "cyan", deg: 90 }}
              radius={"lg"}
              leftSection={
                <IconSearch
                  style={{ width: rem(18), height: rem(18) }}
                  stroke={1.5}
                />
              }
              onClick={() => relatedSearch("success")}
            >
              success
            </Button>
            <Button
              variant="gradient"
              gradient={{ from: "blue", to: "cyan", deg: 90 }}
              leftSection={
                <IconSearch
                  style={{ width: rem(18), height: rem(18) }}
                  stroke={1.5}
                />
              }
              radius={"lg"}
              color="Primary.2"
              onClick={() => relatedSearch("Cricket")}
            >
              Cricket
            </Button>
            <Button
              variant="gradient"
              gradient={{ from: "blue", to: "cyan", deg: 90 }}
              leftSection={
                <IconSearch
                  style={{ width: rem(18), height: rem(18) }}
                  stroke={1.5}
                />
              }
              color="Primary.2"
              radius={"lg"}
              onClick={() => relatedSearch("true")}
            >
              true
            </Button>
          </Group>
        </Box>

        <Space h="sm" />

        <div>
          {value !== "" && searchResult ? (
            searchResult.length > 0 ? (
              <SearchResultDisplay
                data={searchResult}
                relatedSearch={relatedSearch}
              />
            ) : (
              <p>No results found.</p>
            )
          ) : null}
        </div>
      </div>
    </Container>
  );
}
